* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  box-sizing: border-box;
  background-color: beige;
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.search-form {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.search-btn {
  color: #fff;
  cursor: pointer;
  background-color: #1cb992;
  border: none;
  border-radius: 7.5px;
  padding: 10px 20px;
  font-size: 18px;
  box-shadow: 0 0 6px rgba(0, 0, 0, .2);
}

.search-input {
  color: #333;
  max-width: 400px;
  background-color: rgba(28, 185, 146, .5);
  border: none;
  border-radius: 7.5px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  font-size: 18px;
}

.more-btn {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #1cb992;
  border: none;
  border-radius: 7.5px;
  margin-top: 35px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px;
  font-size: 18px;
  display: block;
  box-shadow: 0 0 6px rgba(0, 0, 0, .2);
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 30px auto;
  display: flex;
}

.photo-card {
  width: 30%;
  border-radius: 5px;
  margin-bottom: 60px;
  overflow: hidden;
  box-shadow: 0 0 6px rgba(0, 0, 0, .2);
}

.photo-card img {
  width: 100%;
  height: 50%;
  object-fit: cover;
  transition: opacity .2s ease-in-out;
}

.info {
  color: #333;
  text-transform: capitalize;
  padding: 10px;
  font-size: 20px;
  text-decoration: none;
  display: block;
}

